<script>
jalaliDatepicker.startWatch();
import OneModal from '../../components/OneModal.vue';
import Header from '../../components/Header.vue'
import axios from 'axios'
export default {
    name:'Shift',
    components:{Header,OneModal},
    data() {
        return {
            content:"تعریف شیفت کاری",
            msg:"شیفت جدید با موفقیت افزوده شد",
            list:null,
            filter:''
        }
    },
    created(){
        axios.get('../setshift.json')
        .then(({data})=>{
            this.list=data
        })
    },
    computed: {
         filteredRows() {
            return this.list.filter(part => {
            const name = part.name.toString().toLowerCase();
            const code = part.code.toString().toLowerCase();
            const searchTerm = this.filter.toLowerCase();
            return name.includes(searchTerm) || code.includes(searchTerm);
            });
         }
    },
}
</script>



